.dashboard {
  font-family: 'Roboto', sans-serif;
}

.main-content {
  background-color: #fff;
  padding: 0 8%;
}

.content-area {
  position: relative;
  width: 100%;
  padding-top: 0;
  background: #fff;
}


.empty-state {
    width: 50%;
    margin: 16% auto 0;
    text-align: center;
}

.empty-state h2 {
    margin-top: 8px;
    margin-bottom: 16px;
    font-weight: 300;
    font-size: 24px;
    letter-spacing: 0.4px;
    color: #393939;
}

.loader-container {
    width: 50%;
    margin: 20% auto 0;
    text-align: center;
}

.loader {
    margin: 0 auto;
    border: 2px solid #f3f3f3;
    border-top: 2px solid #02adfd;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    -webkit-animation: spin 0.8s linear infinite;
            animation: spin 0.8s linear infinite
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }

}

@keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.header {
  position: relative;
  width: 100%;
  height: 80px;
  padding: 20px 0;
  margin-top: 1px;
  background-color: #fff;
  border-bottom: 1px solid rgba(193, 193, 193, 0.35);
}

.header:after {
  content: "";
  display: table;
  clear: both;
}

.contact-list__img {
  width: 80px;
  height: 80px;
  border-radius: 100px;
  border: 3px solid #212529;
}

.table th.contact-list__th {
  border-top: 0;
}

.contact-list__th--avatar {
  width: 180px;
}

.table td.contact-list__td {
  vertical-align: middle;
}

.contacts__home {
  padding: 40px 0;
}

.contact-form {
  max-width: 700px;
  border: 1px solid #ddd;
  padding: 30px;
}

.contacts__create {
    padding: 40px 0;
}

.back-btn {
  display: inline-block;
  margin-right: 8px;
  border: 0;
  background: transparent;
  box-shadow: none;
  color: #222;
  cursor: pointer;
}

.back-btn i {
  font-size: 35px;
  position: relative;
  top: 6px;
}

.not-found {
    position: relative;
    width: 100%;
    height: 100vh;
    text-align: center;
    background: #0a3f6f;
}

.centered-content {
  padding: 240px 0;
  color: #fff;

  img {
    width: 20%;
  }

  h1 {
    font-size: 120px;
    margin: 0;
  }

  p {
    font-weight: 300;
    font-size: 24px;
  }
}

